import { ACTION_LOGOUT, ACTION_LOGIN } from '../actions/appActions';

interface IAppReducerState {
    logout: boolean,
    greetMessage: string;
}

const initialState: IAppReducerState = {
    greetMessage: 'Hello guest!',
    logout: false
};

export function reducer(state = initialState, action) {
    switch(action.type) {
        case ACTION_LOGOUT:
            return{
                ...state,
                logout: false
            }
        case ACTION_LOGIN:
            return{
              ...state,
              logout: true
            }
    }
}
